@import "./general";

/* Header */

.c-header__link {
  color: $color-contrast-light;

  &:hover,
  &:focus,
  &:active {
    color: $color-contrast-light;
  }
}

/* End Header */


.row {
  justify-content: center;
}

.box {
  margin: 15px 0;
  padding: 20px;
}

.card-item {
  padding: 10px;
  background: $white;
  @include border(1px, $color-4-400, solid);
  border-radius: 6px;
  margin: 10px;

  &:hover {
    box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.2), 0px 4px 5px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.14);
  }

  &--counter {
    max-width: 80px;
    @include border($color-4-500, 1px);
  }
}

.form-adicionar {
  display: flex;
  justify-content: center;
  align-items: center;

  &--btn {
    font-size: 1.5em;
    border-radius: 100%;
    @include border($color-1-500, 2px);
    height: 25px;
    width: 25px;
    padding: 6px;
    color: $color-contrast-light;
    background-color: $color-1-500;

    &:hover,
    &:focus,
    &:active {
        color: $color-1-500;
        @include border(2px, $color-1-500, solid);
        background-color: $white;
    }
  }
}

._button {
  @extend %button-main;
}

.no-stock {
  font-size: 16px;
  color: $color-contrast-dark;
  font-weight: bold;
  line-height: 100px;
}