@import './general';

#painellogin {
  background: url('https://bluecycle.vteximg.com.br/arquivos/bg-login.jpg') no-repeat;
  margin: 0 auto;
  padding: 0;
  max-width: 100%;
  background-size: cover;
  background-position: center;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0; 

  .painellogin__box {
    margin-top: 20px;
    background: white;
    width: 500px;
    height: 420px;
    margin: 5% auto;

    form {
      flex-direction: column;

      #acessarlogin {
        @extend %button-main;
        width: 50%;
      }
    }

    .painellogin__image {
      text-align: center;
      background: $color-1-500;
      margin-bottom: 25px;

        img {
          width: 280px;
        }
    }
  }
  
}