%button-main{
  display: inline-block;
  padding: 10px;
  color: $white;
  font-weight: bold;
  outline: none;
  text-align: center;
  background: $color-1-500;
  border: 0;
  border-radius: 0;
  margin-top: 20px;

  &:hover {
      text-decoration: none;
      color: $white;
      background: $color-1-600;
  }

  &:active,
  &:focus {
      text-decoration: none;
      color: $white;
      background: $color-1-700;
  }
}